html {
  font-family: source-han-sans-simplified-c, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 62.5%;
}

.App {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  overflow: hidden;
}

.Fit-width-img {
  object-fit: cover;
  width: 100%;
}

.Cloud-float1 {
  object-fit: cover;
  width: 100%;
  animation: Cloud-float1 10s linear 0s infinite alternate;
}

@keyframes Cloud-float1 {
  from {
    left: -30%;
  }

  to {
    left: -20%;
  }
}

.Cloud-float2 {
  transform: rotate(5deg);
  animation: Cloud-float2 10s linear 0s infinite alternate;
  ;
}

@keyframes Cloud-float2 {
  from {
    left: -30%;
  }

  to {
    left: -20%;
  }
}

.Cloud-float3 {
  transform: scaleY(-1) rotate(30deg);
  animation: Cloud-float3 10s linear 0s infinite alternate;
  ;
}

@keyframes Cloud-float3 {
  from {
    left: 20%;
  }

  to {
    left: 10%;
  }
}

.Cloud-float4 {
  transform: rotate(-10deg);
  animation: Cloud-float4 10s linear 0s infinite alternate;
}

@keyframes Cloud-float4 {
  from {
    left: 70%;
  }

  to {
    left: 60%;
  }
}

.Plane-fly {
  transform: rotate(-15deg);
  animation: Plane-fly 6s cubic-bezier(.36,.16,.8,.02) infinite;
}

@keyframes Plane-fly {
  from {
    left: -20vw;
    top: 85vw;
    width: 30vw;
  }

  to {
    left: 60vw;
    top: 10vw;
    width: 250vw;
  }
}